import uniNavBar from '../../components/uni-nav-bar/uni-nav-bar.vue';

export default {
	components: {
		uniNavBar
	},
	data() {
		return {
			getType: "所有人可见",
			itemList: ['A', 'B', 'C'],
		}
	},
	methods: {
		changelook() {
			uni.showActionSheet({
			    itemList: this.itemList,
			    success: (res)=> {
			        console.log('选中了第' + (res.tapIndex + 1) + '个按钮');
					this.getType = this.itemList[res.tapIndex]
			    },
			    fail: function (res) {
			        console.log(res.errMsg);
			    }
			});
		},
		back() {
			uni.navigateBack({
				delta: 1
			})
		}
	}
}
